*{
    margin:0;
    padding:0;
    box-sizing: border-box;
    background-color: white;
    background-color:white;
    font-family: 'Ubuntu', sans-serif;;
}

.navbar{
    /* background-color: blue; */
}

.navbar-brand{
    font-size: 2rem !important;
    color: #9699fa !important;
    font-weight: bold;

}

.navbar a{
    text-shadow: 5px 5px 5px 5px;
    font-size: 1.2rem;
    text-transform: capitalize;
    transition:all 0.3s;
}

.menu_active {
    font-weight: bold;
    border-bottom: 1px solid #565387;
}

.navbar a:hover {
    color:rgb(43, 43, 254) !important;
    -webkit-text-decoration: solid;
            text-decoration: solid;
    font-weight: bolder;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    
}

@media (max-width: 991px){
    .navbar-brand {
        font-size: 1.5rem !important;
    }
    .menu_active {
        font-weight: bold;
        border-bottom: none;
    }
}

.btn-get-started{

    border:solid 1px rgba(80, 80, 240, 0.847);
    -webkit-text-decoration: solid;
            text-decoration: solid;
    font-weight: bolder;
    background-color: white;
    border-radius: 30px;
    padding: 12px;
    color: rgba(80, 80, 240, 0.847);
    letter-spacing: 1px;
    box-shadow: 1px 1px 1px 1px grey;
}

.btn-get-started:hover{
    color: aliceblue;
    background-color: rgb(43, 43, 254);
    transition: 0.5s;
    
}

